<script>
import Footer from "@/components/Footer";
import Comment from "@/components/Comment";

export default {
  name: "BlogDetail",
  components: {
    Comment,
    Footer
  },
  data() {
    return {
      blogId: this.$route.query.blogId,
      blog: {},
      tagsArr: [],
      recommendList: [],

    }
  },
  created() {

    this.load()
    this.$request.put('/blog/updateReadCount/' + this.blogId)

    // this.loadComment()
  },
  methods: {
    scrollToTop() {
      // ‰ΩøÁî®JavaScriptÂÆûÁé∞Âπ≥ÊªëÊªöÂä®ÂõûÂà∞È°µÈù¢È°∂ÈÉ®
      window.scrollTo({
        top: 0,
        behavior: 'smooth',
      });
    },


    setLikes() {
      this.$request.post('/likes/set', {  fid: this.blogId, module: 'ÂçöÂÆ¢' }).then(res => {
        if (res.code === '200') {
          // this.$message.success('Êìç‰ΩúÊàêÂäü')

          this.load()  // ÈáçÊñ∞Âä†ËΩΩÊï∞ÊçÆ
        }
      })
    },
    setCollect() {
      this.$request.post('/collect/set', {  fid: this.blogId, module: 'ÂçöÂÆ¢' }).then(res => {
        if (res.code === '200') {
          // this.$message.success('Êìç‰ΩúÊàêÂäü')

          this.load()  // ÈáçÊñ∞Âä†ËΩΩÊï∞ÊçÆ
        }
      })
    },
    load() {
      this.$request.get('/blog/selectById/' + this.blogId).then(res => {
        this.blog = res.data || {}
        this.tagsArr = JSON.parse(this.blog.tags || '[]')
      })


      this.$request.get('/blog/selectRecommend/' + this.blogId).then(res => {
        this.recommendList = res.data || []
      })
    }
  }
}
</script>

